import React, { Component } from "react";
import { Icon, Menu, Dropdown } from "antd";
import { withTranslation } from "react-i18next";
import dayjs from "dayjs";
import "./style.scss";
import { storage } from "../../util/util";
import { getLanguage } from "../../util/getLanguage";

@withTranslation()
class Languageselect extends Component {
  // TODO: 处理语言名称的函数还需要优化
  state = {
    languagelist: [
      { id: 2, language: "English", abbreviation: "en" },
    ],
    languageNav: false,
    languageinit: [],
  };
  componentDidMount() {
    let index = this.state.languagelist.findIndex((item) => {
      return item.abbreviation === getLanguage();
    });

    this.setState({ languageinit: this.state.languagelist[index] });
  }
  componentWillUnmount() { }

  chooseLanguage = ({ key }) => {
    this.setState({ languageinit: this.state.languagelist[key] }, () => {
      const { i18n } = this.props;
      storage.set("language", this.state.languageinit.abbreviation);
      i18n.changeLanguage(this.state.languageinit.abbreviation);
      if (this.state.languageinit.abbreviation === "zh") {
        dayjs.locale("zh-cn");
      }
      if (this.state.languageinit.abbreviation === "en") {
        dayjs.locale("en");
      }
    });
  };
  render() {
    const { languagelist, languageinit } = this.state;
    const menu = (
      <Menu onClick={this.chooseLanguage}>
        {languagelist.map((item, index) => {
          return (
            <Menu.Item key={index} className="headBar_span">
              <span style={this.props.txtHide ? { margin: '0 5px' } : {}}>{item.language}</span>
            </Menu.Item>
          );
        })}
      </Menu>
    );

    return (
      <Dropdown
        overlay={menu}
        placement="bottomCenter"
        trigger={["hover", "click"]}
      >
        <div
          className={`select-language treeNodeUnselectable`}
          onClick={this.selectLanguage}
          style={this.props.txtHide ? { margin: '0',padding:'0' } : {}}
        >
          <Icon style={{ fontSize: "22px" }} type="global" />
          {this.props.txtHide ? null : <span className="language">{languageinit.language}</span>}
          {this.props.txtHide ? null : <Icon type="down" />}
        </div>
      </Dropdown>
    );
  }
}

export default Languageselect;
