import React from "react";
import {
  Icon,
  Tooltip,
  Modal,
  Avatar,
  Menu,
  Dropdown,
  Spin,
  Badge,
  Drawer,
} from "antd";
import { inject, observer } from "mobx-react";
import { withRouter } from "react-router-dom";
import { withTranslation } from "react-i18next";
import dayjs from "dayjs";
import "./style.scss";
import { host, wsget } from "../../ws";
import { storage, session, removeCookie } from "../../util/util";
import Languageselect from "../../common/Languageselect";
import event from "../../util/event";
import { buyUrl } from "../../util/url";
import { getLanguage } from "../../util/getLanguage";
import i18next from "i18next";

const { confirm } = Modal;

@withRouter
@withTranslation()
@inject("login", "photoalbum", "rahmen", "playlist", "uploadFiles","feedback")
@observer
class HeaderBar extends React.Component {
  constructor(props) {
    super(props);
    this.headBar = React.createRef();
    this.state = {
      languageNav: false,
      selectUser: false,
      user_data: {},
      languagelist: [
        { id: "3", language: "English", abbreviation: "en" },
      ],
      languageinit: {},
    };
  }

  componentDidMount() {
    // this.profile = event.on("setNickname", (res) => {
    //   this.props.login.getUser_detail();
    // });

    this.avatar = event.on("avatar", (id) => {
      this.props.login.getUser_detail();
    });

    let index = this.state.languagelist.findIndex((item) => {
      return item.abbreviation === getLanguage();
    });
    this.setState({ languageinit: this.state.languagelist[index] });

    document.addEventListener("click", this.hide);
  }
  componentWillUnmount() {
    document.removeEventListener("click", this.hide);
    event.rm(this.profile);
    event.rm(this.avatar);
  }

  hide = () => {
    this.setState({ selectUser: false });
  };

  selectInfo = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    this.setState({ selectUser: !this.state.selectUser });
  };
  //跳转个人信息页
  goPersonalDetails = () => {
    this.props.history.push("/personal");
  };

  clear = () => {
    storage.remove("user");
    storage.remove("Z_H_U_DATA");
    session.remove("token");
    removeCookie("cookie");
    this.props.login.clearData();
    this.props.photoalbum.clearData();
    this.props.rahmen.clearData();
    this.props.playlist.clearData();
    this.props.uploadFiles.clearData();
    this.props.feedback.reset();
    wsget("user_logout", null);
    this.props.history.push("/login");
  };

  //退出登录
  loginout = () => {
    let _this = this;
    confirm({
      title: this.props.t("loginout?"),
      okText:i18next.t('Determine'),
      cancelText:i18next.t('Cancel'),
      onOk() {
        _this.clear();
      },
      onCancel() {},
    });
  };

  toClear = () => {
    this.props.history.push(`/clear`);
  };

  getPopupContainer = () => {
    return this.headBar.current;
  };

  goFeedback = () => {
    this.props.history.push(`/feedback`);
  };

  dropdownClick = ({ key }) => {
    if (key === "0") {
      this.goPersonalDetails();
    } else if (key === "1") {
      this.goFeedback();
    } else if (key === "2") {
      this.loginout();
    }
  };

  DrawerTopClick = ({ key }) => {
    switch (key) {
      case "0":
        this.goPersonalDetails();
        break;
      case "1":
        this.goFeedback();
        break;
      case "2":
        this.chooseLanguage(0);
        break;
      case "3":
        this.chooseLanguage(1);
        break;
      case "5":
        this.loginout();
        break;
      default:
        break;
    }
    this.props.onClose();
  };

  chooseLanguage = (key) => {
    this.setState({ languageinit: this.state.languagelist[key] }, () => {
      const { i18n } = this.props;
      storage.set("language", this.state.languageinit.abbreviation);
      i18n.changeLanguage(this.state.languageinit.abbreviation);
      if (this.state.languageinit.abbreviation === "zh") {
        dayjs.locale("zh-cn");
      }
      if (this.state.languageinit.abbreviation === "en") {
        dayjs.locale("en");
      }
    });
  };

  render() {
    const { t, onClose, visible, user } = this.props;
    const { selectUser, languageinit, languagelist } = this.state;
    const { user_info } = this.props.login;

    const menu = (
      <Menu onClick={this.dropdownClick}>
        <Menu.Item className="headBar_span" key="0">
          <span>{t("account")}</span>
        </Menu.Item>
        <Menu.Item className="headBar_span" key="1">
          <span>
            {user_info.unread && <Badge status="error" />}
            {t("Feedback")}
          </span>
        </Menu.Item>
        <Menu.Divider />
        <Menu.Item className="headBar_span" key="2">
          <span>{t("Logout")}</span>
        </Menu.Item>
      </Menu>
    );

    return (
      <div className="headerbar" ref={this.headBar}>
        <div className="bars">
          <Languageselect />
          {user && (
            <Dropdown
              overlay={menu}
              placement="bottomCenter"
              trigger={["hover", "click"]}
            >
              <div
                className={`select-user treeNodeUnselectable ${
                  selectUser ? "act-user" : null
                }`}
                onClick={this.selectInfo}
              >
                <Badge count={user_info.unread ? user_info.unread : 0}>
                  {user_info.profile ? (
                    <Avatar
                      size={40}
                      src={`${host()}/uf/s/${user_info.profile.id}/${
                        user_info.id
                      }`}
                    />
                  ) : (
                    <Avatar size={40} icon="user" />
                  )}
                </Badge>

                <Tooltip
                  placement="left"
                  overlayClassName="tooltip"
                  title={
                    user_info.nickname ? user_info.nickname : user_info.name
                  }
                >
                  {user_info.name ? (
                    <span className="user-name ellipsis">
                      {user_info.nickname ? user_info.nickname : user_info.name}
                    </span>
                  ) : (
                    <span
                      style={{ textAlign: "center", width: "60px" }}
                      className="user-name"
                    >
                      <Spin />
                    </span>
                  )}
                </Tooltip>
                <Icon type="down" />
              </div>
            </Dropdown>
          )}
        </div>

        <Drawer
          placement="top"
          closable={false}
          onClose={onClose}
          visible={visible}
          height="auto"
          destroyOnClose
        >
          <Menu onClick={this.DrawerTopClick} mode="inline" selectable={false}>
            <Menu.Item key="0">
              <span>
                {t("account")}
                {" "}({user_info.nickname ? user_info.nickname : user_info.name})
              </span>
            </Menu.Item>
            <Menu.Item key="1">
              <span>
                {user_info.unread && <Badge status="error" />}
                {t("Feedback")}
              </span>
            </Menu.Item>
            <Menu.SubMenu
              key="sub1"
              title={
                languageinit.language
                  ? t("Language").format(languageinit.language)
                  : "Language"
              }
            >
              {languagelist.map((item) => {
                return <Menu.Item key={item.id}>{item.language}</Menu.Item>;
              })}
            </Menu.SubMenu>
            {buyUrl ? <Menu.Item key="4">
              <a target="_blank" rel="noopener noreferrer" href={buyUrl}>
                {t("Buyingphotoframes")}
              </a>
            </Menu.Item>: null }
            <Menu.Divider />
            <Menu.Item key="5">
              <span>{t("Logout")}</span>
            </Menu.Item>
          </Menu>
        </Drawer>
      </div>
    );
  }
}

export default HeaderBar;
