import i18next from 'i18next';
import { session, storage, setCookie } from './util/util'
import WsMQ from './util/wsmq';
import event from './util/event'
import { message } from 'antd';
import login from './store/login/index'
import { getLanguage } from './util/getLanguage';
import { dec } from './util/b64';

export const MINI_SYSTEM_ERROR = 100;
export const ERROR_RESPONSE = 123;
export const GET_TIME_ERROR = 124;
export const GET_TIMEOUT = 125;
export const NO_CONNECT = 126;
export const NO_MATCH = 127;

// 一些配置信息
const VERSION = 20
const HOSTS = JSON.parse(dec(``))


let CONNECT = false; // eslint-disable-line

function nexthost() {
  // 检查最后一次登录
  const host = storage.get('wsok')
  if (host && host.try < 2) {
    return host
  }
  // 返回一个随机地址
  // TODO: 最好能根据地区来选择地址
  return HOSTS[Math.floor(Math.random() * HOSTS.length)]
}

function okhost(host) {
  host.try = 0
  storage.set('wsok', host)
}


async function ws_login() {
  const user = storage.get('user');
  if (user && user.name && user.pwd) {
    try {
      const data = await wsget('user_login', { name: user.name, pwd: user.pwd })
      login.login_in2(data);
      session.set('token', data.token)
      storage.set('user', { ...user, ...data })
      setCookie('cookie', data.token, 0)
      event.emit('isAutoLogin', true);
      return true;
    } catch (res) {
      if (window.ws.isconnected() && res.state !== 0) {
        autoLogin()
      } else if (res.state === 0) {
        event.emit('isAutoLogin', false)
        return false;
      }
    }
  } else {
    if (localStorage.getItem('user') !== null) {
      event.emit('isAutoLogin', false)
    }
    return false;
  }
}

export async function autoLogin() {
  if (JSON.stringify(login.user_info) === "{}") {
    ws_login();
  } else {
    if (CONNECT) {
      ws_login();
    }
  }
}


export default function ws() {
  if (!window.ws) {
    window.ws = new WsMQ(
      `/api/web/${getLanguage()}/${VERSION}`,
      nexthost,
      async (host) => { // openfun
        console.log('ws connected.', host); // eslint-disable-line
        event.emit('wsinit', 1)
        CONNECT = true;
        autoLogin()
        okhost(host)
      },
      async (host) => { // closefun
        console.log('ws closed.', host); // eslint-disable-line
        event.emit('wsinit', 2)
        host.try = (host.try || 0) + 1
        storage.set('wsok', host)
        CONNECT = false;
      }
    )
  }
  return window.ws;
}
