import { storage } from './util';
import dayjs from 'dayjs';
//import 'dayjs/locale/zh-cn'
import 'dayjs/locale/en'

export function getLanguage() {
  let type = navigator.appName;
  let lang;
  try {
    if (storage.get('language')) {
      let initday = storage.get('language');
      if (initday === 'en') {
        dayjs.locale('en');
      }
      return initday;
    } else {
      if (type === "Netscape") {
        lang = navigator.language;//获取浏览器配置语言，支持非IE浏览器
      } else {
        lang = navigator.userLanguage;//获取浏览器配置语言，支持IE5+ == navigator.systemLanguage
      };
      let initlang = lang.substr(0, 2);
      dayjs.locale('en');
      return 'en';
    }
  } catch (error) {
    if (type === "Netscape") {
      lang = navigator.language;//获取浏览器配置语言，支持非IE浏览器
    } else {
      lang = navigator.userLanguage;//获取浏览器配置语言，支持IE5+ == navigator.systemLanguage
    };
    let initlang = lang.substr(0, 2);
    if (initlang === 'en') {
      dayjs.locale('en');
    }
    return initlang;
  }
}
