import React from "react";
import { get } from "mobx";
import { Icon, Progress } from "antd";
import i18next from "i18next";
import { color } from "../../progresscolor";
import { observer } from "mobx-react";

@observer
class ListCell extends React.Component {
  state = {};
  render() {
    const { item, retry } = this.props;
    return (
      <li className='up_list_cell'>
        <Icon type="picture" theme="filled" />
        <span className="filename">{item.name}</span>
        <div className="right">
          {get(item, "delete") && (
            <Icon
              onClick={() => {
                this.props.delete(item);
              }}
              className="cancelup"
              type="close"
            />
          )}

          {get(item, "invalid") && get(item, "awite") && (
            <span className="hashErr">{i18next.t("Invalidpicture")}</span>
          )}
          {get(item, "unsuccessfully") && (
            <span
              className="retry"
              onClick={() => {
                retry(item);
              }}
            >
              {i18next.t("retry")}
            </span>
          )}
          {get(item, "invalid") ? null : (
            <span className="awaitUp">
              {get(item, "awite")
                ? `${i18next.t("uploading2")}···`
                : `${i18next.t("waiting")}···`}
            </span>
          )}

          <Progress
            className="list-progress"
            trailColor="#C5C5C5"
            strokeColor={color}
            strokeWidth={17}
            showInfo={false}
            type="circle"
            percent={item.percent}
            width={24}
          />
        </div>
      </li>
    );
  }
}

export default ListCell;
