import { observable, action, set, values, remove } from "mobx";
import { wsget, host } from "../../ws";
import { ajaxPromise, changeHash, getCookie } from "../../util/util";
import event from "../../util/event";
import { message } from "antd";

// const getIdInit = function (id) {
//   return function () {
//     return id++;
//   };
// };

// let getId = getIdInit(1);
const num = 3;
const timer = 5000;

class uploadFiles {
  @observable visible = false;
  @observable files = {};
  @observable dis = true; //上传进度列表显隐标识
  @observable finish = false;
  @observable drawer_visible = false;
  @observable small_dis = false;
  idkey = 1;
  noUploads = 0; //超出限制的文件个数
  compFlag = true; //是否压缩标记
  key = 1; //正在上传的位置
  interrupt = false; //全部清除标识
  playlist = {};
  winNum = 0;
  failNum = 0;
  retryList = [];

  //文件处理
  @action upFile = (filelist, id, reset, root) => {
    this.visible = true;
    this.dis = true;
    this.small_dis = true;
    this.drawer_visible = true;
    this.interrupt = false;

    for (let value of filelist) {
      if (/image\/\w+/.test(value.type)) {
        if (value.size > 10485760) {
          this.noUploads++;
          continue;
        }
      } else if (/video\/\w+/.test(value.type)) {
        if (value.size > 52428800) {
          this.noUploads++;
          continue;
        }
      } else {
        if (!this.typeErr) {
          this.typeErr = true;
          message.info("Only pictures and videos can be uploaded");
        }
        continue;
      }
      const file = {
        to: id,
        file: value,
        name: value.name,
        percent: 0,
        succeed: false,
        wrong: false,
        awite: false,
        delete: true,
        invalid: false,
        unsuccessfully: false,
        up: false,
      };
      if (root) {
        file.root = root;
      }
      if (!/image\/\w+/.test(value.type)) {
        file.video = true;
      }
      const k = this.idkey;
      this.idkey++;
      file.id = k;
      set(this.files, k, file);
    }

    this.typeErr = false;

    reset && reset(); //清理文件input的value

    if (values(this.files).length > 0) {
      if (!root && !this.playlist[id]) {
        this.playlist[id] = [];
      }
      if (!this.saveTimer) {
        this.saveFile();
      }

      if (this.compFlag) {
        this.compFlag = false;
        this.finish = true;
        this.winNum = 0;
        this.failNum = 0;
        this.key = values(this.files)[0].id;
        if (window.ws.isconnected) {
          for (let index = 0; index < num; index++) {
            const item = this.files[this.key];
            if (item) {
              this.up_hash(item);
              this.key++;
            }
          }
          this.eventOn();
        } else {
          event.on("isAutoLogin", (res) => {
            if (res) {
              for (let index = 0; index < num; index++) {
                const item = this.files[this.key];
                if (item) {
                  this.up_hash(item);
                  this.key++;
                }
              }
              this.eventOn();
            }
          });
        }
      }
    }
  };

  @action eventOn = () => {
    if (!this.connect) {
      this.connect = event.on("wsinit", async (res) => {
        if (res === 2) {
          clearInterval(this.saveTimer);
          this.saveTimer = null;
        }
      });
    }

    if (!this.Logined) {
      this.Logined = event.on("isAutoLogin", (res) => {
        if (res) {
          this.save_to_server();
          if (!this.saveTimer) {
            this.saveFile();
          }
          this.retryList = [];
          const list = values(this.files);
          if (list.length) {
            this.key = list[0].id;
            for (let index = 0; index < num; index++) {
              const item = this.files[this.key];
              if (item) {
                item.percent = 0;
                this.up_hash(item);
                this.key++;
              }
            }
          }
        }
      });
    }
  };

  @action up_hash = async (item) => {
    if (!item.succeed) {
      try {
        item.delete = false;
        item.awite = true;
        const hash = await changeHash(item, { quality: 0.7 }, 2048, 2048);
        item.hash = hash;
        wsget("file_check_hash", hash.data.hash)
          .then((data) => {
            if (!this.interrupt) {
              if (data.id > 0) {
                //存在，直接添加
                if (window.ws.isconnected()) {
                  item.percent = 100;
                  const filedata = { id: data.id };
                  item.up = true;
                  remove(this.files, item.id);
                  if (data.ext) {
                    filedata.ext = data.ext;
                  }
                  this.nextUp({
                    id: item.to,
                    data: filedata,
                    root: item.root,
                  });
                }
              } else if (data.id === 0) {
                //不存在，上传
                this.up_server(item);
              }
            }
          })
          .catch((error) => {
            if (window.ws.isconnected()) {
              if (error.state === 125) {
                this.up_hash(item);
              } else {
                item.delete = true;
                item.unsuccessfully = true;
                item.invalid = true;
                item.awite = false;
                item.up = true;
                this.key++;
                const obj = this.files[this.key];
                if (obj) {
                  this.up_hash(obj);
                }
              }
            }
          });
      } catch (error) {
        console.log(error);
        if (window.ws.isconnected()) {
          item.invalid = true;
          item.delete = true;
          this.key++;
          const obj = this.files[this.key];
          if (obj) {
            this.up_hash(obj);
          }
        }
      }
    } else {
      remove(this.files, item.id);
      this.key++;
      const obj = this.files[this.key];
      if (obj) {
        this.up_hash(obj);
      }
    }
  };

  @action up_server = (item) => {
    const url = `${host()}/up/${item.video ? "v" : "p"}/${getCookie(
      "cookie"
    )}/${item.hash.data.hash}/${item.name}`;
    ajaxPromise(item.hash.data.blob, url, this.uploadProgress, item.id)
      .then((result) => {
        if (!item.delete && !this.interrupt && window.ws.isconnected()) {
          item.percent = 100;
          const { data } = result;
          item.up = true;
          remove(this.files, item.id);
          this.nextUp({
            id: item.to,
            data,
            root: item.root,
          });
        }
      })
      .catch((error) => {
        console.log(error);
        if (window.ws.isconnected()) {
          item.delete = true;
          item.unsuccessfully = true;
          item.invalid = true;
          item.awite = false;
          item.up = true;
          this.key++;
          const obj = this.files[this.key];
          if (obj) {
            this.up_hash(obj);
          }
        }
      });
  };

  @action nextUp = (obj) => {
    this.winNum++;
    if (obj.root) {
      const data = {
        id: obj.id,
        list: [obj.data],
      };
      wsget("album_add_files", data).then(() => {
        const item = this.files[this.key];
        if (!item) {
          if (values(this.files).filter((item) => !item.up).length === 0) {
            this.modalHide();
            this.small_close();
            event.emit("up_refresh");
          }
        }
      });
      event.emit("albumupload", data);
    } else {
      const data = {
        id: obj.id,
        list: obj.data,
      };
      this.playlist[obj.id].unshift(obj.data);
      event.emit("savefiles", data);
    }
    if (this.retryList.length) {
      const arr = this.retryList.splice(0, 1);
      const item = this.files[arr.id];
      this.up_hash(item);
    } else {
      const item = this.files[this.key];
      if (item) {
        this.up_hash(item);
        this.key++;
      } else {
        //全部上传过了
        let arr = values(this.files);
        if (arr.filter((item) => !item.up).length === 0) {
          console.log("上传完了");
          this.compFlag = true;
          this.dis = false;
          this.drawer_visible = false;
          this.finish = false;
          clearInterval(this.saveTimer);
          this.saveTimer = null;
          this.save_to_server(() => {
            this.modalHide();
            this.small_close();
            event.emit("up_refresh");
          });
        }
      }
    }
  };

  //播放列表的定时器
  saveFile = () => {
    this.saveTimer = setInterval(this.save_to_server, timer);
  };
  @action save_to_server = (cb) => {
    if (window.ws.isconnected()) {
      for (const k in this.playlist) {
        if (this.playlist.hasOwnProperty(k)) {
          const item = this.playlist[k];
          if (item.length > 0) {
            const list = item.splice(0, item.length);
            const data = {
              id: Number(k),
              list,
              index: 0,
            };
            wsget("playlist_save_to", data).then((msg) => {
              event.emit("playupload", { msg, id: data.id });
              cb && cb();
            });
          }
        }
      }
    }
  };

  //上传进度监听
  @action uploadProgress = (ev, id) => {
    if (ev.lengthComputable && !this.interrupt) {
      const precent = Math.floor((ev.loaded / ev.total) * 100);
      this.files[id].percent = precent;
    }
  };

  //重试上传
  @action retry = async (item) => {
    item.delete = false;
    item.unsuccessfully = false;
    item.invalid = false;
    item.awite = false;
    item.up = false;
    if (this.compFlag) {
      this.up_hash(item);
    } else {
      this.retryList.push(item);
    }
  };

  //删除上传
  @action delete = (item) => {
    item.succeed = true;
  };

  //缩小
  @action is_dis = () => {
    this.dis = !this.dis;
  };

  //上传进度隐藏
  @action modalHide = () => {
    this.visible = false;
    event.rm(this.Logined);
    event.rm(this.connect);
    this.Logined = null;
    this.connect = null;
  };

  @action showDrawer = () => {
    this.drawer_visible = true;
  };

  @action onClose = () => {
    this.drawer_visible = false;
  };

  @action small_close = () => {
    this.small_dis = false;
    event.rm(this.Logined);
    event.rm(this.connect);
    this.Logined = null;
    this.connect = null;
  };

  //清除全部
  @action abandon = () => {
    this.interrupt = true;
    this.compFlag = true;
    clearInterval(this.saveTimer);
    this.saveTimer = null;
    this.save_to_server();
    this.dis = false;
    this.drawer_visible = false;
    this.modalHide();
    this.winNum = 0;
    this.failNum = 0;
    this.files = {};
    this.finish = false;
    let arr = values(this.files);
    if (arr.length) {
      this.key = arr[arr.length - 1].id + 1;
    }
    this.retryList = [];
  };

  @action clearData = () => {
    this.visible = false;
    this.files = {};
    this.dis = true; //上传进度列表显隐标识
    this.finish = false;
    this.noUploads = 0; //超出限制的文件个数
    this.compFlag = true; //是否压缩标记
    this.key = 1; //正在上传的位置
    this.idkey = 1;
    this.interrupt = false; //全部清除标识
    this.playlist = {};
    this.winNum = 0;
    this.failNum = 0;
    this.retryList = [];
    event.rm(this.Logined);
    event.rm(this.connect);
    this.Logined = null;
    this.connect = null;
  };
}

export default new uploadFiles();
