import { observable, action, set } from 'mobx';
import { wsget } from '../../ws';
import dayjs from 'dayjs';
import { fileSort } from '../../util/util';
import event from '../../util/event';

class bindframe {
  @observable list = null;

  @action initlist = () => {
    wsget('playlist_list', null).then((data) => {
      this.list = fileSort.update(observable(data));;
    })
  }

  //新建
  @action framesbindcreplay = () => {
    this.list.unshift({
      id: -1
    })
    // if (!this.flag) {
    //   this.list.unshift({
    //     id: -1
    //   })
    //   this.flag = true;
    // }
  }

  //新建2
  @action creframesbindplay = (name) => {
    wsget('playlist_create', name, 1).then((data) => {
      this.list[0].id = data;
      set(this.list[0], { name });
      set(this.list[0], { filecount: 0 });
      set(this.list[0], { updated: dayjs().format() });
      // this.flag = false;

      event.emit('createplay')
      // wsget('playlist_list', null).then((data) => {
      //   this.bindframplaylist = fileSort.update(observable(data));
      //   this.bindframplaylist_2 = this.bindframplaylist;
      // })
    })
  }

  //取消新建
  @action close_cre = () => {
    this.list.shift();
    // this.flag = false;
  }

}

export default new bindframe();